#PartZero {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-image: url("/public/Assets/PartZeroImgs/BackgroundIntro.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
}


.ambulance-zero {
    position: absolute;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    width: 90vw;
    height: auto;
}


.divText-Ambulance {
    position: absolute;
    left: 43.6vw;
    top: 30.2vh;
    width: 39vw;
    height: 31.5vh;
    background-color: #f2f2f2;
    text-align: center;
    font-family: 'Assistant', sans-serif;
    color: #454545;
    padding: 3vh 5vh 0vh 5vh;
    font-size: 2.5vmin;
    direction: rtl;
    opacity: 0;
    transition: opacity 0.2s ease;
}


.divText-Ambulance.fade-in {
    opacity: 1;
}


.Tomer-zero {
    opacity: 0;
    transition: opacity 0.5s ease-in;
    position: absolute;
    top: -14vh;
    width: 13vw;
    height: auto;
    left: -34vw;
}


.fade-in {
  opacity: 1;
  transition: opacity 0.5s ease-in;
}


.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease-out;
}


.buttons-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px auto;
    position: relative;
    width: 43vw;
    top: 6vh;
    left: 2vw;
    direction: rtl;
    gap: 33vw;
}


.btn-text {
    display: block;
}


.text-label {
    color: black;
}


.btn-text-next .img-arrow {
    background-image: url('/public/Assets/Btns/arrowBlack.png');
}




.btn-text-prev .img-arrow {
    background-image: url('/public/Assets/Btns/arrowBlack.png');
}




.btn-text-end .img-arrow {
    background-image: url('/public/Assets/Btns/arrowFullBlack.png');
}

